<template lang="pug">
  div
    el-dialog(:visible.sync="editModal" width="60%" title="Тип источника")
      el-form(:inline="true"
        :model="entityForEdit"
        :rules="rules"
        v-if="editModal"
        ref="editForm"
        label-position="top")
        el-form-item(label="Название" prop="title")
          el-input(v-model="entityForEdit.title" placeholder="Введите название")
        el-form-item(label="Действия")
          el-button-group
            el-button(type="success" @click="save" round) Сохранить
            el-button(@click="cancelEdit" round) Отменить
    el-button(type="success" style="float: right" icon="el-icon-plus" @click="edit()" round) Добавить тип источника
    div(style="margin-top:20px")
        el-table(:data="moneySourceTypes")
            el-table-column(label="Название" prop="title")
            el-table-column
                template(slot-scope="scope")
                  el-button-group
                    el-button(size="mini" @click="edit(scope.row)" round) Корректировать
                    el-button(type="danger" size="mini" @click="remove(scope.row)" round) Удалить
</template>

<script>
import { mapActions } from 'vuex'
import BookKeeping from '@/mixins/book-keeping'
import EntityListEdit from '@/mixins/entity-list-edit'
export default {
  name: 'moneySourceTypes',
  mixins: [BookKeeping, EntityListEdit],
  data () {
    return {
      entityForEdit: null,
      editModal: false,
      rules: {
        title: [{ type: 'string', required: true, message: 'Обязательно к заполнению', trigger: 'blur' }]
      }
    }
  },
  methods: {
    ...mapActions([
      'createMoneySourceType',
      'updateMoneySourceType',
      'deleteMoneySourceType'
    ]),
    deleteEntity (entity) {
      return this.deleteMoneySourceType(entity)
    },
    sendEntity (entity) {
      entity.id ? this.updateMoneySourceType(entity) : this.createMoneySourceType(entity)
    },
    createNewEntity () {
      return {
        title: ''
      }
    }
  }
}
</script>

<style>

</style>



