<template lang="pug">
  transition(name="slide-fade")
    el-card.fixed-action-block(v-if="visible")
      slot
</template>

<script>
export default {
  name: 'fixedDrop',
  props: {
    options: {
      type: Object
    }
  },
  data () {
    return {
      visible: false
    }
  },
  created () {
    if (this.options.trigger === 'onscroll') {
      window.onscroll = () => {
        const newStateVisible = window.pageYOffset >= this.options.offsetY
        if (this.visible !== newStateVisible) {
          this.$emit('visible', newStateVisible)
          this.visible = newStateVisible
        }
      }
    }
  }
}
</script>

<style>
    .slide-fade-enter-active {
        transition: all .1s ease;
    }
    .slide-fade-leave-active {
        transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to {
        transform: translateY(-40px);
        opacity: 0;
    }
    .fixed-action-block {
      position: fixed;
      top:0;
      left: 0;
      z-index: 10;
      width: 100%;
      box-shadow: 0 1px 6px rgba(0,0,0,.2);
      border-color: #eee;
    }
</style>



