<template lang="pug">
    div
      el-dialog(title="Детали по источникам"
        :visible.sync="modalVisibile"
        style="z-index: 12")
        p(style="line-height:2" ) {{ moneySourceType(currentMoneySourceType).title}}:
          span.just-amount {{ balance(currentMoneySourceType) }}
        el-tabs(v-model="activeTab" @tab-click="handleClick")
          el-tab-pane(v-for="sourceType in moneySourceTypes" :label="sourceType.title" :name="sourceType.id.toString()" :key="sourceType.id")
        el-table(:data="moneySources" border :show-header="false")
          el-table-column(property="title" label="Источник" )
          el-table-column(property="amount" label="Сумма")
            template(slot-scope="scope" )
              template(v-for="(currencyValue, currencyIndex) in scope.row.values")
                span(v-if="currencyValue.currency === 'грн' || (currencyValue.currency !== 'грн' && currencyValue.amount !== 0) " :class="currencyValue.amount > 0 ? 'positive-amount' : 'negative-amount'") {{ currencyIndex > 0 ? ',' : '' }} {{ currencyValue.amount.toLocaleString() }} {{ currencyValue.currency}}
      div
        p(style="line-height:2" v-for="sourceType in moneySourceTypes") {{ sourceType.title}}:
          span.just-amount {{ balance(sourceType.id) }}
        el-button(@click="modalVisibile = true" round size="mini") Детально
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Balance',
  data () {
    return {
      modalVisibile: false,
      activeTab: null,
      moneySources: []
    }
  },
  methods: {
    handleClick (tab, event) {
      this.moneySources = this.balanceSourcesByType(parseInt(tab.name))
    }
  },
  created () {
    this.activeTab = this.moneySourceTypes[0].id.toString()
    this.moneySources = this.balanceSourcesByType(this.moneySourceTypes[0].id)
  },
  computed: {
    currentMoneySourceType () {
      return parseInt(this.activeTab)
    },
    ...mapGetters(['balance', 'moneySourceTypes', 'balanceSourcesByType', 'moneySourceType'])
  }
}
</script>

<style>

</style>



